<template>
	<view class="me">
		<!-- 背景 -->
		<view :style="{backgroundImage: 'linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.5)),url(' + getBackGround() + ')', 'background-repeat':'no-repeat', backgroundSize:'cover',backgroundPosition:'center center'}"  class="bar">
			<!-- <image mode="aspectFill"  class="backgroundImage" src="../../static/logo.jpg"></image> -->
			
			<view  class="mine-user-info">
				<view style="display: flex;align-items: center;" class="mine-avatar">
					<image class="me-user-av"   :src="setUrl(user.avatar)"></image>
					<view style="margin-left: 20px;" @click="follow()" class="follow">
						<uni-tag v-if="isFollow==0" circle="true" text="关注" customStyle="background-color: #fac843;border-color:#fac843"></uni-tag>
						<uni-tag v-else circle="true" text="已关注"></uni-tag>
					</view>
				</view>
				<view  class="mine-username ">
					<span>{{user.nickName}}</span>
					<!-- <span>微信用户</span> --> 
					<image v-if="user.sex==1" style="width: 12px;height: 12px;margin-left: 3px;" src="../../static/images/icon/g (1).png"></image>
					<image v-if="user.sex==0" style="width: 12px;height: 12px;margin-left: 3px;"src="../../static/images/icon/b.png"></image>
				</view>
				<view class="info-list">
					<view @click="gotoFanList()" class="item">
						<span>{{fansNum}}</span>
						<span>粉丝</span>
					</view>
					<view @click="gotoFollowList()" class="item">
						<span>{{followNum}}</span>
						<span>关注</span>
					</view>
				
				</view>
				<view  class="tag-view">
					<view v-for="(item,index) in user.roles" class="item">
						<uni-tag size="small" :inverted="true" :circle="true" :text="item" custom-style="background-color:#4f4f4f; color: #ffffff; "/>
					</view>
				</view>
			</view>
		
	</view>
		
		<view v-if="userId!=null" class="warp">
			
			<talkList  :userId="userId" ref="talkList"></talkList>
		</view>
	</view> 
</template>

<script>
	import {getUserInfoById} from '@/api/user.js';
	import {mapState,mapGetters} from "vuex";
	import talkList from "@/components/talkList/talkList.vue";
	import {getFollowInfoById,follow} from "@/api/user.js";
	export default {
		data() {
			return {
				status:0,
				user:{},
				followNum:0,
				fansNum:0,
				isFollow:0,
				userId:null,
				barImage:null
			
			
			}
		},
		comments:{
			talkList
		},
		onLoad(op) {
			this.userId = op.userId
			getUserInfoById({userId:op.userId}).then(res=>{
				this.user = res.data
			})
			this.getFollowInfo()
			
		},
		//下拉刷新
		onPullDownRefresh() {
			this.getFollowInfo();
			this.$refs.talkList.reFresh()
		},
		methods: {
			getFollowInfo(){
				//获取关注、粉丝信息
				getFollowInfoById({userId:this.userId}).then(res=>{
					this.followNum = res.data.followNum
					this.fansNum  = res.data.fansNum
					this.isFollow = res.data.isFollow
					this.barImage = res.data.barImage
				})
			},
			//关注
			follow(){
				this.isFollow^=1;
				//关注他
				follow(this.userId);
			},
		
			getBackGround(){
				if(this.stringUtils.isNoEmpty(this.barImage))
					return this.getUrl(this.barImage)
				return this.getUrl(this.user.avatar);
			},
			setUrl(url){
				return this.getUrl(url);
			},
			gotoLogin(){
				uni.navigateTo({
					url:'/pages/login/login'
				})
			},
			gotoFollowList(){
				uni.navigateTo({
					url:`/pages/follow/follow?userId=${this.user.userId}`
				})
			},
			gotoFanList(){
				uni.navigateTo({
					url:`/pages/fans/fans?userId=${this.user.userId}`
				})
			}
		},
		//触底
		onReachBottom(){
			this.$refs.talkList.handleCurrentChange();
			// console.log("触底了")
		}
	}
</script>

<style>
	/* 个人信息开始 */
	/* 背景 */
.bar{
	/* background-image: ; */
	position: relative;
	width: 100%; 
	height: 45vh;
	
	border-style: solid;
	border-width: 0 0 2px 0;
	border-color: black;
	/* filter: brightness(50%); */
	
	
}
.backgroundImage{
	/* background-image: ; */
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
}

/* 头像 */
.me-user-av{
	position: relative;
	width: 70px;
	height: 70px;
	border-radius: 50%;
	border-style: solid;
	border-color: white;
	border-width: 3px;
	background: linear-gradient(-180deg, #BCC5CE 0%, #929EAD 98%), radial-gradient(at top left, rgba(255,255,255,0.30) 0%, rgba(0,0,0,0.30) 100%);
	 background-blend-mode: screen;
				
}
.mine-user-info{
	position: absolute;
	left: 10%;
	bottom: 15px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;	
}

.mine-username{
	margin-top: 10px;
	color: white;
	font-size: 18px;	
}

.info-list{
	padding: 0 85rpx 0 25rpx;
	display: flex;
	
}
.info-list > .item{
	
	width: 145rpx;
	height: 145rpx;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
}
.info-list > .item > span:first-child{
	font-size: 16px;
	margin-bottom: 5px;
	font-weight: 600;
}
.info-list > .item > span:nth-child(2){
	font-size: 12px;
}
.tag-view{
	display: flex;
	
	margin-top: 15px; 
	font-size: 10px;
	
	
}
.tag-view   > .item {
	margin-left: 10px;
}
.tag-view  uni-tag:nth-child(n+2){
	margin-left: 5px;
}
/* 个人信息结束 */

/* 列表开始*/
.item-list{
	display: flex;
	flex-wrap: wrap;
	margin: 20rpx 25rpx;
	background-color: white;
	box-shadow: 0 3px 8px 0 rgba(0,0,0,.03);
	border-radius: 10px;
}
.item-list >.item{
	/* box-sizing: border-box; */
	width: 175rpx;
	height: 175rpx;
	/* background-color: aqua; */
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	
	
}



</style>
